import {
    Component,
    h,
    Host,
    Prop,
    State,
    Element,
    Method,
    Event,
    EventEmitter,
    Watch,
} from '@stencil/core';
import {
    computePosition,
    flip,
    shift,
    offset,
    size,
    autoUpdate,
    type Placement,
} from '@floating-ui/dom';

/**
 * @category Overlays
 * @description A popover menu component for displaying action lists when via click or hover on a trigger element.
 *
 * @slot menu-trigger - Trigger element for the menu (only used when trigger_id is not provided)
 * @slot default - Content to display inside the menu
 */

export type Position = 'top' | 'bottom' | 'left' | 'right';
export type Align = 'start' | 'end';
export type OpenOn = 'hover' | 'click';
export type AriaRole = 'menu' | 'listbox';

@Component({
    tag: 'at-menu',
    shadow: false,
})
export class AtMenu {
    /**
     * Menu's x offset from edge in pixels. Only applied for origin_x = 'start' | 'end'
     */
    @Prop() offset_x: number = 0;

    /**
     * Menu's y offset from edge in pixels. Only applied for origin_y = 'top' | 'bottom'
     */
    @Prop() offset_y: number = 0;

    /**
     * Position of opened menu element relative to the trigger element.
     */
    @Prop() position: Position = 'bottom';

    /**
     * Alignment of opened menu element relative to trigger element.
     */
    @Prop() align: Align = 'start';

    /**
     * String representing the 'width' style of the menu element ('auto' or 'NUMpx'). When not specified, defaults to trigger element width.
     * To fit menu to content use width="fit-content" - Avoid width='auto' as this will result in 100% width.
     */
    @Prop() width?: string;

    /**
     * Prevent closing of menu when options are selected. Used for multi-selection controls.
     */
    @Prop() autoclose: boolean = true;

    /**
     * Event type that triggers the menu open state. Click or Hover.
     */
    @Prop() trigger: OpenOn = 'click';

    /**
     * Close the menu when the user clicks within the menu panel. Default for single selection menus.
     */
    @Prop() role: AriaRole = 'menu';

    /**
     * Prevent opening menu
     */
    @Prop() disabled: boolean = false;

    /**
     * Data-id of an external element to use as the trigger. When provided, the trigger slot is not needed.
     */
    @Prop() trigger_id?: string;

    @Watch('disabled')
    disabledChanged(newValue: boolean) {
        if (newValue && this.isOpen) {
            this.closeMenu();
        }
    }

    @State() isOpen = false;
    triggerEl: HTMLElement;
    menuEl: HTMLElement;
    private triggerEls: HTMLElement[] = [];
    private popoverId: string;
    private cleanupAutoUpdate?: () => void;
    private updatePosition = async () => {
        if (this.triggerEl && this.menuEl && this.isOpen) {
            await this.updateFloatingPosition();
        }
    };

    @Element() el: HTMLElement;

    /**
     * Toggles the dropdown menu's open state.
     */
    @Method()
    async toggleMenu(): Promise<void> {
        if (this.disabled) {
            return;
        }

        if (this.menuEl) {
            this.menuEl.togglePopover();
            this.isOpen = this.menuEl.matches(':popover-open');
        }
        this.atuiMenuStateChange.emit(this.isOpen);
        await this.updatePosition();
        this.updateAriaExpanded();
    }

    /**
     * Opens the dropdown menu.
     */
    @Method()
    async openMenu(): Promise<void> {
        if (this.disabled) {
            return;
        }

        if (this.menuEl) {
            this.menuEl.showPopover();
            this.isOpen = true;
        }
        this.atuiMenuStateChange.emit(true);
        await this.updatePosition();
        this.updateAriaExpanded();
    }

    /**
     * Closes the dropdown menu.
     */
    @Method()
    async closeMenu(): Promise<void> {
        if (this.menuEl) {
            this.menuEl.hidePopover();
            this.isOpen = false;
        }
        this.atuiMenuStateChange.emit(false);
        this.updateAriaExpanded();
    }

    private updateAriaExpanded(): void {
        if (this.trigger_id && this.triggerEl) {
            this.triggerEl.setAttribute(
                'aria-expanded',
                this.isOpen ? 'true' : 'false',
            );
        }
    }

    /**
     * Return the current menu open state
     */
    @Method()
    async getIsOpen(): Promise<boolean> {
        return this.isOpen;
    }

    /**
     * Emits an event containing the open menu state.
     */
    @Event() atuiMenuStateChange: EventEmitter<boolean>;

    private timedOutCloser;

    async componentDidLoad() {
        this.popoverId = `atui-menu-${Math.random().toString(36).substr(2, 9)}`;

        if (this.trigger_id) {
            this.triggerEls = Array.from(
                document.querySelectorAll(`[data-id="${this.trigger_id}"]`),
            ) as HTMLElement[];
            if (this.triggerEls.length === 0) {
                console.warn(
                    `atui-menu: No elements found with data-id="${this.trigger_id}"`,
                );
                return;
            }
        } else {
            this.triggerEl = this.el.querySelector(
                '[data-name="menu-trigger"]',
            );
        }

        await this.setupFloatingUI();
        setTimeout(() => this.setupPopoverEventListeners(), 0);

        if (this.trigger_id && this.triggerEls.length) {
            this.setupExternalTriggerListeners();
        }
    }

    private setupPopoverEventListeners() {
        if (this.menuEl) {
            this.menuEl.addEventListener('toggle', (event: Event) => {
                const customEvent = event as any;
                this.isOpen = customEvent.newState === 'open';
                this.atuiMenuStateChange.emit(this.isOpen);

                if (this.isOpen) {
                    requestAnimationFrame(() => this.updatePosition());
                }
            });
        }
    }

    private setupExternalTriggerListeners() {
        if (!this.triggerEls || this.triggerEls.length === 0) return;

        this.triggerEls.forEach((el) => {
            const listeners: Array<{ event: string; handler: EventListener }> =
                [];

            if (this.trigger === 'hover') {
                const mouseEnterHandler = () => {
                    if (!this.disabled) {
                        this.triggerEl = el;
                        this.mouseEnterHandler();
                    }
                };

                const mouseLeaveHandler = () => {
                    if (!this.disabled) {
                        this.triggerEl = el;
                        this.mouseLeaveHandler();
                    }
                };

                listeners.push(
                    { event: 'mouseenter', handler: mouseEnterHandler },
                    { event: 'mouseleave', handler: mouseLeaveHandler },
                );
            }

            if (this.trigger === 'click') {
                const clickHandler = async (event: Event) => {
                    if (!this.disabled) {
                        event.preventDefault();
                        event.stopPropagation();
                        this.triggerEl = el;
                        if (this.isOpen) {
                            await this.closeMenu();
                        } else {
                            await this.openMenu();
                        }
                    }
                };

                listeners.push({ event: 'click', handler: clickHandler });
            }

            const keydownHandler = async (event: KeyboardEvent) => {
                if (!this.disabled) {
                    this.triggerEl = el;
                    switch (event.key) {
                        case 'Escape':
                            await this.closeMenu();
                            break;
                        case 'Enter':
                        case ' ':
                            event.preventDefault();
                            await this.toggleMenu();
                            break;
                    }
                }
            };

            listeners.push({ event: 'keydown', handler: keydownHandler });

            listeners.forEach(({ event, handler }) => {
                el.addEventListener(event, handler);
                this.externalTriggerListeners.push({
                    element: el,
                    event,
                    handler,
                });
            });

            el.setAttribute('aria-haspopup', 'true');
            el.setAttribute('aria-expanded', 'false');
        });
    }

    disconnectedCallback() {
        this.cleanupFloatingUI();
        this.cleanupExternalTriggerListeners();
    }

    private externalTriggerListeners: Array<{
        element: HTMLElement;
        event: string;
        handler: EventListener;
    }> = [];

    private cleanupExternalTriggerListeners() {
        this.externalTriggerListeners.forEach(({ element, event, handler }) => {
            element.removeEventListener(event, handler);
        });
        this.externalTriggerListeners = [];
    }

    private async mouseEnterHandler(): Promise<void> {
        if (this.timedOutCloser) clearTimeout(this.timedOutCloser);
        if (!this.isOpen) {
            await this.openMenu();
        }
    }

    private async mouseLeaveHandler(): Promise<void> {
        await this.closeMenu();
    }

    private async setupFloatingUI(): Promise<void> {
        if (this.cleanupAutoUpdate) {
            this.cleanupAutoUpdate();
        }

        if (this.triggerEl && this.menuEl) {
            const observer = new IntersectionObserver(
                (entries) => {
                    if (!entries[0].isIntersecting && this.isOpen) {
                        this.mouseLeaveHandler();
                    }
                },
                { threshold: 0 },
            );
            observer.observe(this.triggerEl);

            this.cleanupAutoUpdate = autoUpdate(
                this.triggerEl,
                this.menuEl,
                () => {
                    if (this.isOpen) {
                        const placement = this.getFloatingUIPlacement();
                        const strategy = 'fixed';

                        computePosition(this.triggerEl, this.menuEl, {
                            placement,
                            strategy,
                            middleware: [
                                offset(
                                    this.position === 'bottom' ||
                                        this.position === 'top'
                                        ? this.offset_y
                                        : this.offset_x,
                                ),
                                flip({
                                    fallbackStrategy: 'bestFit',
                                    padding: 8,
                                }),
                                shift({
                                    padding: 8,
                                    mainAxis: true,
                                    crossAxis: true,
                                }),
                                size({
                                    apply({
                                        availableWidth,
                                        availableHeight,
                                        elements,
                                    }) {
                                        Object.assign(elements.floating.style, {
                                            maxWidth: `${availableWidth}px`,
                                            maxHeight: `${availableHeight}px`,
                                        });
                                    },
                                }),
                            ],
                        }).then(({ x, y }) => {
                            const styles: any = {
                                position: strategy,
                                left: `${x}px`,
                                top: `${y}px`,
                                margin: '0',
                                transform: 'none',
                            };

                            if (this.width) {
                                styles.width = this.width;
                            }

                            Object.assign(this.menuEl.style, styles);
                        });
                    }
                },
                {
                    ancestorScroll: true,
                    ancestorResize: true,
                    elementResize: true,
                    layoutShift: true,
                    animationFrame: true,
                },
            );

            const originalCleanup = this.cleanupAutoUpdate;
            this.cleanupAutoUpdate = () => {
                originalCleanup();
                observer.disconnect();
            };
        }
    }

    private cleanupFloatingUI() {
        if (this.cleanupAutoUpdate) {
            this.cleanupAutoUpdate();
            this.cleanupAutoUpdate = undefined;
        }
    }

    private async updateFloatingPosition(): Promise<void> {
        if (!this.triggerEl || !this.menuEl) return;
        await this.setupFloatingUI();
    }

    private getFloatingUIPlacement(): Placement {
        const positionMap: Record<string, string> = {
            top: 'top',
            bottom: 'bottom',
            left: 'left',
            right: 'right',
        };

        const alignMap: Record<string, string> = {
            start: 'start',
            end: 'end',
        };

        const position = positionMap[this.position] || 'bottom';
        const align = alignMap[this.align] || 'start';

        return `${position}-${align}` as Placement;
    }

    render() {
        return (
            <Host
                class="relative"
                onBlur={() =>
                    this.trigger === 'click' && !this.disabled
                        ? this.mouseLeaveHandler()
                        : null
                }
            >
                {!this.trigger_id && (
                    <div
                        aria-haspopup="true"
                        data-name="menu-trigger"
                        ref={(el) => (this.triggerEl = el)}
                        aria-expanded={`${this.isOpen ? 'true' : 'false'}`}
                        onMouseEnter={() =>
                            this.trigger === 'hover' && !this.disabled
                                ? this.mouseEnterHandler()
                                : null
                        }
                        onKeyDown={async (event) => {
                            switch (event.key) {
                                case 'Escape':
                                    await this.closeMenu();
                                    break;
                                case 'Enter':
                                case ' ':
                                    event.preventDefault();
                                    await this.toggleMenu();
                                    break;
                            }
                        }}
                        onMouseLeave={() =>
                            this.trigger === 'hover' && !this.disabled
                                ? this.mouseLeaveHandler()
                                : null
                        }
                        onClick={async (event) => {
                            if (this.trigger === 'click' && !this.disabled) {
                                event.preventDefault();
                                event.stopPropagation();
                                if (this.isOpen) {
                                    await this.closeMenu();
                                } else {
                                    await this.openMenu();
                                }
                            }
                        }}
                        class={this.disabled ? 'contents' : ''}
                    >
                        <slot name="menu-trigger" />
                    </div>
                )}

                <div
                    role={this.role}
                    data-position={this.position}
                    data-align={this.align}
                    ref={(el) => (this.menuEl = el)}
                    aria-hidden={`${this.isOpen ? 'false' : 'true'}`}
                    popover="auto"
                    id={this.popoverId}
                    onMouseEnter={() =>
                        this.trigger === 'hover' &&
                        !this.disabled &&
                        this.mouseEnterHandler()
                    }
                    onMouseLeave={() =>
                        this.trigger === 'hover' &&
                        !this.disabled &&
                        this.mouseLeaveHandler()
                    }
                    onKeyDown={async (event) => {
                        if (
                            event.key === 'Enter' ||
                            event.key === ' ' ||
                            event.key === 'Escape'
                        ) {
                            if (this.autoclose) {
                                await this.mouseLeaveHandler();
                            }
                        }
                    }}
                    onClick={() => this.autoclose && this.mouseLeaveHandler()}
                    class="w-fit rounded-md bg-white p-4 shadow-md"
                    data-name="menu-content-wrapper"
                >
                    <slot />
                </div>
            </Host>
        );
    }
}
